import {
    mapState,
    mapMutations
} from 'vuex';
import store from "@/store/index.js";
import api from "@/api/api.js";
export default {
    computed: mapState(['hasLogin', 'userInfo', 'token']),
    data() {
        return {
            male: '男',
            female: '女'
        };
    },
    onShow() {
        api.req.getUserInfo()
    },
    methods: {
        chooseImage() {
            uni.chooseImage({
                count: 1, //默认9
                // sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
                sourceType: ['album'], //从相册选择
                success: function(res) {
                    console.log(JSON.stringify(res.tempFilePaths));
                    userInfo.headUrl = res.tempFilePaths[0];
                }
            });
        },
        saveChange() {
            if (this.userInfo.name.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '用户名不能为空'
                });
                return;
            } else if (this.userInfo.phone.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '手机号不能为空'
                });
                return;
            } else if (this.userInfo.sex.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '性别不能为空'
                });
                return;
            } else if (this.userInfo.studentNum.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '学号不能为空'
                });
                return;
            } else if (this.userInfo.major.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '专业不能为空'
                });
                return;
            } else if (this.userInfo.college.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '学院不能为空'
                });
                return;
            } else if (this.userInfo.mail.length == 0) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '邮箱不能为空'
                });
                return;
            }
            if (this.userInfo.phone.length != 11) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '手机号为11位数字'
                });
                return;
            }
            if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.userInfo.phone))) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '请检查手机号是否正确'
                });
                return;
            }
            if (this.userInfo.sex != this.male && this.userInfo.sex != this.female) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '性别只能为“男”或“女”'
                });
                console.log(this.userInfo.sex)
                return;
            }
            if (this.userInfo.studentNum.length != 9) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '请输入9位数正确学号'
                });
                return;
            }
            var reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
            if (!reg.test(this.userInfo.mail)) {
                this.loading = false;
                uni.showToast({
                    icon: 'none',
                    title: '请输入有效邮箱地址'
                });
                return;
            }
            var that = this
            var url = api.urls.changeUserInfomation;
            var data = {
                name: that.userInfo.name,
                phone: that.userInfo.phone,
                sex: that.userInfo.sex,
                studentNum: that.userInfo.studentNum,
                major: that.userInfo.major,
                college: that.userInfo.college,
                mail: that.userInfo.mail
            };

            api.req.put(url, data, (res) => {
                if (res.code === 0) {
                    uni.showToast({
                        icon: 'none',
                        title: '修改成功！'
                    });
                    uni.navigateBack()
                } else {
                    uni.showModal({
                        content: res.msg,
                        showCancel: false
                    })
                }
            });
        }
    },
}
